.pagination{
  margin-top: 2.5em;
  ul{
    display: flex;
    align-items: center;
  }
  li{
    min-width: 1.875em;
    height: 3.125em;
    margin: 0 .125em;
    &.active{
      pointer-events: none;
      a{
        color: $black;
      }
    }
  }
  .prev,
  .next{
    @include bgImgSize(.5em);
  }
  .prev{
    background-image: url(../img/prev2.svg);
    margin-left: 0;
  }
  .next{
    background-image: url(../img/next2.svg);
    margin-right: 0;
  }
  a{
    padding: 0 .25em;
    @include center;
    min-width: 100%;
    height: 100%;
    font-size: 1.25em;
    font-weight: 700;
    color: rgba($black, 0.5);
    &:hover{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .pagination{
    margin-top: 1.75em;
    ._hide-mob{
      display: none;
    }
    ul{
      font-size: .75em;
    }
    li{
      margin: 0 .5em;
    }
  }
}
