.catalog{
  &__groups{
    margin-top: 1.81em;
  }
}

.catalog-group{
  margin-bottom: 6.25em;
  &:last-child{
    margin-bottom: 0;
  }
  &__title{
    font-size: 2.5em;
    line-height: 120%;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__groups{
      margin-top: 2.25em;
    }
  }

  .catalog-group{
    margin-bottom: 4.875em;
    &__title{
      font-size: 1.5625em;
    }
  }
}


