.contacts-page{
  padding: 2.5em 0 5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 38.6875em) 36.5em;
    justify-content: space-between;
    gap: 2.5em 1em;
    margin-top: 3.125em;
  }
  &__items{
    @include grid100gap(3.75em);
  }
  &__map{
    height: 21.25em;
    margin-top: 1.25em;
    iframe{
      width: 100%;
      height: 100%;
    }
  }
  &__btn{
    width: 100%;
    height: 4.75em;
    margin-top: 3.75em;
    span{
      font-size: 1.375em;
    }
    img{
      margin-right: 1.25em;
      width: 2.5625em;
    }
  }
  .tabs{
    margin-top: 2.5em;
  }
}

.contacts-form{
  padding: 5em 4em 8.19em 2.5em;
  background-color: #F0F0F0;
  clip-path: polygon( 100% 12.72%,100% 12.72%,99.963% 12.315%,99.854% 11.928%,99.679% 11.562%,99.443% 11.223%,99.151% 10.915%,98.808% 10.643%,98.418% 10.411%,97.988% 10.224%,97.522% 10.087%,97.025% 10.004%,3.874% 0.136%,3.874% 0.136%,3.267% 0.115%,2.683% 0.176%,2.132% 0.314%,1.624% 0.522%,1.168% 0.792%,0.773% 1.118%,0.449% 1.494%,0.206% 1.913%,0.053% 2.368%,0% 2.852%,0% 87.279%,0% 87.279%,0.037% 87.685%,0.146% 88.072%,0.321% 88.438%,0.557% 88.777%,0.849% 89.085%,1.192% 89.357%,1.582% 89.589%,2.012% 89.776%,2.478% 89.913%,2.975% 89.995%,96.126% 99.864%,96.126% 99.864%,96.733% 99.885%,97.317% 99.824%,97.867% 99.686%,98.376% 99.478%,98.832% 99.208%,99.227% 98.882%,99.551% 98.506%,99.794% 98.087%,99.947% 97.632%,100% 97.148%,100% 12.72% );
  &__top{
    @include grid100gap(1.25em);
  }
  &__desc{
    font-size: 1.125em;
    line-height: 117%;
    opacity: .6;
  }
  &__fields{
    margin-top: 1.25em;
    @include grid100gap(1.19em);
  }
  &__btn{
    height: 4.1875em;
    span{
      font-size: 1.375em;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts-page{
    padding: 1.875em 0 3.75em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2.5em;
    }
    .tabs{
      margin-top: 1.25em;
      @include fullScroll;
      white-space: nowrap;
    }
    &__items{
      gap: 1.25em;
    }
    &__btn{
      margin-top: 1.25em;
      padding: 0 1.25em;
      width: auto;
      height: 3.625em;
      span{
        font-size: .9375em;
      }
      img{
        width: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts-page{
    .contacts-item{
      &__title{
        font-size: 1.05em;
      }
      &__desc{
        font-size: 1.57em;
      }
    }
  }

  .contacts-form{
    clip-path: polygon( 100% 12.374%,100% 12.374%,99.95% 11.985%,99.804% 11.61%,99.57% 11.255%,99.251% 10.924%,98.857% 10.621%,98.391% 10.35%,97.861% 10.115%,97.273% 9.92%,96.634% 9.77%,95.949% 9.67%,5.949% 0.083%,5.949% 0.083%,5.033% 0.032%,4.146% 0.072%,3.304% 0.195%,2.522% 0.394%,1.818% 0.663%,1.206% 0.993%,0.702% 1.378%,0.323% 1.81%,0.083% 2.282%,0% 2.787%,0% 87.626%,0% 87.626%,0.05% 88.015%,0.196% 88.39%,0.43% 88.745%,0.749% 89.076%,1.143% 89.379%,1.609% 89.65%,2.139% 89.885%,2.726% 90.08%,3.366% 90.229%,4.051% 90.33%,94.051% 99.917%,94.051% 99.917%,94.967% 99.968%,95.854% 99.928%,96.696% 99.805%,97.478% 99.606%,98.182% 99.337%,98.794% 99.007%,99.298% 98.622%,99.677% 98.19%,99.917% 97.718%,100% 97.213%,100% 12.374% );
    padding: 6em 1.875em;
  }
}