.tabs{
  display: flex;
}

.tab{
  color: rgba($black,.6);
  transition: all .3s;
  @include lastM(0 2em 0 0);
  span{
    font-size: 1.125em;
    line-height: 120%;
  }
  &:hover{
    background: #FBFBFB;
    cursor: pointer;
  }
  &._active{
    font-weight: 700;
    color: $black;
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
