.blog-page{
  &__grid{
    margin-top: 2.5em;
    row-gap: 2.5em;
  }
}

.condition,
.article{
  .text{
    ul,ol,
    p{
      opacity: 0.6;
    }
    img{
      width: calc(100% + 25em);
      margin-bottom: 1.375em;
      border-radius: 1.375em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}

.article{
  &__inner{
    width: 100%;
    max-width: 48.75em;
  }
}

.blog-main{
  color: $white;
  &__inner{
    min-height: 34.375em;
    position: relative;
    display: flex;
    align-items: flex-end;
    padding: 3.75em 2.75em;
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 41.125em;
    @include grid100gap(1.125em);
  }
  &__desc{
    font-size: 1.375em;
    line-height: 118%;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 1.375em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .blog-page{
    &__grid{
      margin-top: 1.25em;
    }
  }

  .article{
    .text{
      img{
        width: 100%;
        margin-bottom: .9375em;
        border-radius: .5em;
      }
      h3{
        max-width: 84%;
      }
    }
  }

  .blog-main{
    &__inner{
      padding: 1.375em .9375em;
    }
    &__content{
      gap: 1.1875em;
    }
    &__desc{
      font-size: .9375em;
    }
  }
}


@media screen and (max-width: 576px){
  .blog-main{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}