.header{
  padding: 1.125em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 16em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-left: 4em;
  }
}

.burger{
  display: none;
}

.nav{
  ul{
    display: flex;
  }
  li{
    margin-right: 1.4375em;
    &:last-child{
      margin: 0;
    }
  }
  a{
    opacity: .6;
    font-size: .875em;
    line-height: 120%;
    &:hover{
      color: $black;
      opacity: 1;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .75em 0;
    &__logo{
      width: 9.75em;
      z-index: 11;
      position: relative;
    }
    &__btn{
      margin: 2em 0 0;
      width: 100%;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    transform: unset;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 6em 0 0;
    z-index: 10;
    &__scroll{
      padding: 1.25em;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      align-items: flex-start;
      overflow-y: auto;
      height: 100%;
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 3.25em;
    height: 3.5625em;
    display: flex;
    margin-left: 1em;
    background-image: url(../img/mask4.svg);
    @include bgImgSize(100%);
    img{
      @include centerAbsolute;
      width: 1.875em;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 1.5em;
    }
  }

  .nav{
    ul{
      display: block;
    }
    li{
      margin: 0 0 1em;
    }
  }
}


