.text{
  line-height: 140%;
  h1,h2{
    margin-bottom: .25em;
  }
  h3,h4,h5{
    margin-bottom: .6em;
  }
  p{
    font-size: 1.375em;
    line-height: 140%;
    @include lastM(0 0 1em);
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      position: relative;
      &:before{
        content: '•';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  hr{
    height: 2em;
    display: block;
    margin: 0;
    opacity: 0;
  }
}



@media screen and (max-width: 992px){
  .text{
    p{
      font-size: .9375em;
    }
    hr{
      height: 1em;
    }
  }
}


