.index-catalog{
  overflow: hidden;
  &__top{
    max-width: 55em;
  }
}

.product-collection{
  margin-top: 2.5em;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
  @media screen and (min-width: 993px){
    .swiper{
      &-nav{
        display: none;
      }
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 5.25em / 4));
        gap: 1.75em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
        height: 100%;
      }
    }
  }
}

.product-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(100%);
    margin-bottom: 1.25em;
    &:before{
      content: '';
      position: absolute;
      top: 1px;
      left: .75em;
      height: calc(100% - 2px);
      width: calc(100% - 3em);
      clip-path: polygon( 100% 21.613%,100% 21.613%,99.917% 20.6%,99.677% 19.625%,99.288% 18.702%,98.761% 17.839%,98.108% 17.049%,97.337% 16.341%,96.459% 15.727%,95.486% 15.218%,94.426% 14.824%,93.29% 14.557%,9.957% 0.268%,9.957% 0.268%,8.426% 0.129%,6.942% 0.227%,5.533% 0.546%,4.226% 1.064%,3.046% 1.764%,2.021% 2.627%,1.177% 3.633%,0.541% 4.764%,0.14% 6.001%,0% 7.325%,0% 78.387%,0% 78.387%,0.083% 79.4%,0.323% 80.375%,0.712% 81.298%,1.239% 82.161%,1.892% 82.952%,2.663% 83.659%,3.541% 84.273%,4.514% 84.782%,5.574% 85.176%,6.71% 85.443%,90.043% 99.732%,90.043% 99.732%,91.574% 99.871%,93.058% 99.772%,94.467% 99.454%,95.774% 98.936%,96.954% 98.236%,97.979% 97.373%,98.823% 96.367%,99.459% 95.236%,99.86% 93.999%,100% 92.675%,100% 21.613% );
      background-color: #F0F0F0;
    }
  }
  &__desc{
    margin-top: 1.25em;
  }
  &__bottom{
    padding-top: .875em;
    margin-top: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__btn{
    margin-top: .875em;
  }
}

.swiper-nav{
  display: flex;
  align-items: center;
  margin-top: 2.375em;
  .swiper-button{
    position: static;
    margin: 0 1.25em 0 0;
  }
}

.swiper-button{
  width: 2.625em;
  height: auto;
  @include center;
  img{
    width: 100%;
  }
}

.geography{
  &__inner{
    margin-top: 1.75em;
    padding-left: 1.875em;
    position: relative;
  }
  &__map{
    max-width: 70.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    @include grid100gap(1.25em);
    width: 21em;
    padding: 1.875em;
    background: $white;
    box-shadow: 0 .25em 2.75em rgba(0, 0, 0, 0.25);
    position: absolute;
    right: -1.375em;
    bottom: 1.75em;
  }
}


@media screen and (max-width: 992px){
  .product-collection{
    margin-top: 2.375em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
        width: 19.375em;
      }
    }
  }

  .geography{
    &__inner{
      padding-left: 0;
      padding-bottom: 10em;
    }
    &__info{
      bottom: 0;
      left: 0;
      right: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .geography{
    overflow: hidden;
    &__map{
      width: 30em;
      margin-left: -.25em;
    }
  }
}