.contacts{
  margin-bottom: 2.5em;
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4em));
    gap: 6em;
  }
}

.contacts-item{
  &__title{
    font-size: 1.5em;
    line-height: 140%;
  }
  &__desc{
    font-size: 2.25em;
    line-height: 140%;
    font-weight: 700;
  }
  &__title2{
    font-size: 1.875em;
    line-height: 120%;
    font-weight: 700;
  }
  .socials{
    margin-top: 1.25em;
  }
}

.main{
  margin: -.625em 0 1em;
  &__inner{
    display: grid;
    grid-template-columns: 37.6875em calc(100% - 37.6875em - .25em);
    gap: .25em;
  }
  &__col{
    padding-top: 5.81em;
  }
  &__text{
    margin-top: 2.5em;
    max-width: 35.75em;
  }
  &__desc{
    font-size: 1.5em;
    line-height: 140%;
    opacity: 0.67;
    font-weight: 700;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    margin-right: 1.125em;
    min-width: 14.25em;
    &:last-child{
      margin: 0;
    }
  }
}

.main-slider{
  width: 31.25em;
  margin-right: -1.31em;
  margin-left: auto;
  position: relative;
  .swiper{
    &-slide{
      opacity: 0;
      pointer-events: none;
      &-active{
        opacity: 1;
      }
    }
    &-button{
      top: unset;
      bottom: 7.25em;
      &-prev{
        left: -1em;
      }
      &-next{
        right: -1em;
      }
    }
    &-pagination{
      width: auto;
      left: 2.65em;
      bottom: .5em;
      opacity: .3;
      font-family: $font2;
      font-weight: 900;
      font-size: 1.25em;
      display: flex;
      align-items: flex-end;
      ._current{
        font-size: 2.2em;
        margin-bottom: -.125em;
        margin-right: .125em;
      }
      ._all{
        margin-left: .125em;
      }
    }
  }
}

.main-card{
  &__top{
    padding-bottom: 110%;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      clip-path: polygon( 100% 20.737%,100% 20.737%,99.96% 20.218%,99.845% 19.719%,99.659% 19.245%,99.407% 18.803%,99.093% 18.398%,98.724% 18.035%,98.304% 17.72%,97.837% 17.459%,97.329% 17.257%,96.784% 17.119%,4.784% 0.145%,4.784% 0.145%,4.049% 0.072%,3.336% 0.122%,2.659% 0.285%,2.031% 0.55%,1.464% 0.909%,0.971% 1.352%,0.566% 1.868%,0.26% 2.449%,0.067% 3.084%,0% 3.763%,0% 79.263%,0% 79.263%,0.04% 79.782%,0.155% 80.281%,0.341% 80.755%,0.593% 81.197%,0.907% 81.602%,1.276% 81.965%,1.696% 82.28%,2.163% 82.541%,2.671% 82.744%,3.216% 82.881%,95.216% 99.855%,95.216% 99.855%,95.951% 99.928%,96.664% 99.878%,97.341% 99.715%,97.969% 99.45%,98.536% 99.091%,99.029% 98.648%,99.434% 98.132%,99.74% 97.551%,99.933% 96.916%,100% 96.237%,100% 20.737% );
      background-color: $primary;
      z-index: -1;
    }
  }
  &__img{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    max-width: 130%;
    max-height: 80%;
  }
  &__bottom{
    width: 24.625em;
    padding: 4.44em 1.5em 5.62em;
    margin: -8.875em auto 0;
    position: relative;
    z-index: 3;
    text-align: center;
    @include grid100gap(.625em);
    filter: drop-shadow(0px 11px 55px rgba(0, 0, 0, 0.25));
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      clip-path: polygon( 100% 32.066%,100% 32.066%,99.95% 31.075%,99.803% 30.122%,99.567% 29.218%,99.247% 28.374%,98.849% 27.601%,98.38% 26.908%,97.846% 26.307%,97.254% 25.809%,96.609% 25.423%,95.918% 25.16%,6.07% 0.273%,6.07% 0.273%,5.137% 0.135%,4.233% 0.231%,3.374% 0.541%,2.577% 1.048%,1.857% 1.734%,1.232% 2.578%,0.718% 3.563%,0.33% 4.671%,0.085% 5.882%,0% 7.179%,0% 67.934%,0% 67.934%,0.05% 68.925%,0.197% 69.878%,0.433% 70.782%,0.753% 71.626%,1.151% 72.399%,1.62% 73.092%,2.154% 73.693%,2.746% 74.191%,3.391% 74.577%,4.082% 74.84%,93.93% 99.727%,93.93% 99.727%,94.863% 99.865%,95.767% 99.77%,96.626% 99.459%,97.423% 98.952%,98.143% 98.267%,98.768% 97.422%,99.282% 96.437%,99.67% 95.329%,99.915% 94.118%,100% 92.821%,100% 32.066% );
      background-color: $white;
    }
  }
  &__title{
    opacity: 0.67;
    text-transform: unset;
  }
  &__price{
    line-height: 115%;
  }
  &__btn{
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    margin-bottom: 1.875em;
    .h2{
      font-size: 1.875em;
      max-width: 90%;
    }
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .contacts-item{
    &._type2{
      margin-top: 1em;
    }
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: 1.5625em;
    }
    &__title2{
      font-size: 1.5625em;
    }
    .socials{
      margin-top: .625em;
    }
  }

  .main{
    margin: 1.75em 0 1.875em;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - 1.5em));
      gap: 1.25em 3em;
    }
    &__col2{
      padding-right: 2em;
    }
    &__text{
      margin-top: 1em;
    }
    &__desc{
      font-size: 1em;
      line-height: 140%;
      font-weight: 400;
    }
    &__nav{
      margin-top: 1.25em;
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 .625em;
    }
  }

  .main-slider{
    font-size: .72em;
    width: 100%;
  }
}


@media screen and (max-width: 576px){
  .main{
    text-align: center;
    &__inner{
      position: relative;
      grid-template-columns: 100%;
      padding-top: 9.625em;
    }
    &__col{
      padding-top: 1em;
    }
    &__col2{
      order: -1;
      padding: 0 2.22em 0;
    }
    &__title{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
    &__text{
      margin: 0;
    }
  }

  .main-slider{
    margin-right: 0;
    .swiper{
      &-pagination{
        font-size: 1em;
        left: 1.65em;
      }
    }
  }

  .main-card{
    &__bottom{
      width: 20em;
      margin-top: -5em;
    }
  }
}