.footer{
  padding: 3em 0 4.25em;
  background: #222;
  color: $white;
  &__logo{
    width: 16em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: 16em 31.25em 17.5em;
    justify-content: space-between;
    gap: 3.125em;
  }
  &__links{
    ul{
      @include grid100gap(.75em);
    }
    a{
      font-size: .9375em;
      line-height: 140%;
    }
  }
  &__c{
    margin-top: 1.25em;
    opacity: .4;
  }
  @media screen and (min-width: 993px){
    .container{
      padding: 0 5em;
    }
  }
}

.footer-nav{
  ul{
    display: grid;
    grid-template-columns: repeat(2, calc(50% - .625em));
    gap: 1.25em;
  }
  a{
    font-size: 1.0625em;
    line-height: 118%;
    opacity: .6;
    &:hover{
      opacity: 1;
    }
  }
}

.footer-subscribe{
  margin-top: 1.25em;
  position: relative;
  &__btn{
    position: absolute;
    top: 50%;
    right: .9375em;
    transform: translateY(-50%);
    width: .875em;
    height: .875em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__field{
    input{
      border: none;
      color: $white;
      width: 100%;
      height: 2.33em;
      font-size: .9375em;
      padding: 0 2.75em 0 1em;
      border-radius: 0.33em;
      background: rgba(255, 255, 255, 0.25);
      &::placeholder{
        color: $white;
      }
    }
  }
}

.footer-socials{
  margin-top: 1.25em;
  &__title{
    opacity: .8;
  }
  .socials{
    &__item{
      width: 1em;
      height: 1em;
      @include lastM(0 .5em 0 0);
      &:not(:hover){
        path{
          fill: $white;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-bottom: 3.75em;
    &__inner{
      grid-template-columns: 100%;
    }
  }
}


