.reviews{
  overflow: hidden;
}

.reviews-collection{
  margin-top: 2.5em;
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      padding-right: 1.875em;
      width: 18em;
    }
  }
}

.review-card{
  display: block;
  &__img{
    @include imgCover(155.2%);
    border-radius: 1.25em;
    &:before{
      content: '';
      @include centerAbsolute;
      z-index: 3;
      width: 3.4375em;
      height: 4em;
      background-image: url(../img/play.svg);
      @include bgImgSize(100%);
    }
  }
}

.faq{
  &__items{
    @include grid100gap(2.5em);
    margin-top: 3em;
    max-width: 71.75em;
  }
}

.qa-card{
  &._active{
    .qa-card__btn svg{
      transform: rotate(-45deg);
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    margin-left: .25em;
    width: .6875em;
    height: .6875em;
    @include center;
    flex-shrink: 0;
    svg,
    img{
      transition: all .3s;
      width: 100%;
    }
  }
  &__hidden{
    padding-top: 1.25em;
    display: none;
  }
}

.blog{
  overflow: hidden;
}

.blog-collection{
  margin-top: 2.5em;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      padding-right: 1.25em;
      width: 25%;
    }
    &-nav{
      display: none;
    }
  }
}

.blog-card{
  display: block;
  &__img{
    @include imgCover(100%);
    border-radius: .625em;
  }
  &__content{
    @include grid100gap(.625em);
    margin-top: .625em;
  }
}


@media screen and (max-width: 992px){
  .reviews-collection{
    margin-top: 1.25em;
    .swiper-nav{
      margin-top: 1.75em;
    }
  }

  .blog-collection{
    margin-top: .875em;
    .swiper{
      &-slide{
        width: 18.75em;
      }
      &-nav{
        display: flex;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .faq{
    &__items{
      gap: 1.25em;
      margin-top: 1.25em;
    }
  }

  .qa-card{
    &__top{
      min-height: 1.75em;
    }
    &__title{
      font-size: .9375em;
    }
    &__hiddem{
      padding-top: .75em;
    }
    .text18{
      font-size: .875em;
    }
  }
}