@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5625em;
    height: 3.5625em;
    border-radius: .3125em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
    }
}

.m-btn-yellow{
    color: $black;
    background-color: $primary;
    &:hover{
        color: $black;
        background-color: darken($primary,10%);
    }
}

.m-btn-black{
    color: $primary;
    background-color: #111;
    &:hover{
        color: $primary;
        background-color: lighten(#111,10%);
    }
}

.m-btn-white{
    color: $black;
    background-color: $white;
    opacity: 0.6;
    box-shadow: 0px 8px 44px 0px rgba(17, 17, 17, 0.15);
    &:hover{
        color: $black;
        opacity: 1;
    }
}