.error404{
  &~.line{
    margin-top: 0;
  }
  &__inner{
    padding: 6.5em 0 8.25em;
    position: relative;
  }
  &__content{
    max-width: 29.625em;
  }
  &__btn{
    width: 100%;
    max-width: 23.8125em;
    margin-top: 2em;
  }
  &__media{
    position: absolute;
    right: -2.125em;
    bottom: 0;
    width: 45.75em;
  }
  &__img{
    width: 100%;
    display: block;
    position: relative;
    z-index: 2;
  }
  &__img2{
    position: absolute;
    width: 56.8125em;
    right: -6.25em;
    top: -1.0625em;
  }
}


@media screen and (max-width: 992px){
  .error404{
    &__inner{
      padding: 9em 0 0;
    }
    &__media{
      position: relative;
      right: -1.5em;
      bottom: unset;
      margin-top: 6.5em;
      font-size: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    &__media{
      font-size: 1em;
      width: calc(100% + 3em);
      right: unset;
      margin-left: -2.5em;
    }
    &__img2{
      width: 23.5625em;
      right: 1.375em;
      top: -1.625em;
    }
  }
}