.line{
  margin: 2.5em 0;
  height: 3.75em;
  background-image: url(../img/line.svg);
  background-size: auto 100%;
  background-repeat: repeat-x;
  background-color: $primary;
}

.how-work{
  overflow: hidden;
  &__top{
    margin: 0 auto;
    width: 56em;
    max-width: 100%;
    text-align: center;
  }
  &__steps{
    display: grid;
    grid-template-columns: repeat(5, calc(20% - 11em / 5));
    gap: 3.5em 2.75em;
    margin-top: 3.75em;
  }
}

.step-item{
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__round{
    background-color: #b2b2b2;
    width: .6875em;
    height: .6875em;
    border-radius: 50%;
    margin: .9375em auto;
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 10000px;
      height: .0625em;
      background-color: #b2b2b2;
      z-index: -1;
    }
  }
}

.why{
  margin-top: -1.25em;
  &__inner{
    position: relative;
    padding: 4.6875em 3em 9.75em;
    background-color: $primary;
    clip-path: polygon( 0% 2.944%,0% 2.944%,0.024% 2.454%,0.094% 1.992%,0.206% 1.564%,0.355% 1.176%,0.538% 0.835%,0.75% 0.548%,0.987% 0.321%,1.245% 0.16%,1.521% 0.073%,1.81% 0.065%,98.419% 11.217%,98.419% 11.217%,98.68% 11.282%,98.926% 11.41%,99.153% 11.598%,99.36% 11.838%,99.543% 12.126%,99.7% 12.457%,99.827% 12.825%,99.921% 13.224%,99.98% 13.65%,100% 14.097%,100% 97.056%,100% 97.056%,99.976% 97.546%,99.906% 98.008%,99.794% 98.436%,99.645% 98.824%,99.462% 99.165%,99.25% 99.452%,99.013% 99.679%,98.755% 99.84%,98.479% 99.927%,98.191% 99.935%,1.58% 88.783%,1.58% 88.783%,1.32% 88.718%,1.074% 88.59%,0.847% 88.402%,0.64% 88.162%,0.457% 87.874%,0.3% 87.543%,0.173% 87.175%,0.079% 86.776%,0.02% 86.35%,0% 85.903%,0% 2.944% );
  }
  &__top{
    max-width: 33em;
  }
  &__items{
    max-width: 40.75em;
    margin-top: 3.875em;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2.875em));
    gap: 5.875em 5.75em;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 32em;
  }
}


@media screen and (max-width: 992px){
  .line{
    height: 2.75em;
    margin: 1.875em 0;
  }

  .how-work{
    &__top{
      text-align: left;
      padding-right: 2em;
    }
    &__steps{
      margin-top: 2.5em;
      grid-template-columns: 100%;
    }
  }

  .step-item{
    align-items: unset;
    text-align: left;
    @include grid100gap(.6875em);
    position: relative;
    padding-left: 2.125em;
    &__round{
      top: .6875em;
      left: 0;
      transform: translateX(-50%);
      position: absolute;
      margin: 0;
      &:before{
        height: 250em;
        width: 2px;
        top: 100%;
        transform: translateX(-50%);
      }
    }
    &:last-child{
      .step-item__round:before{
        background-color: $white;
        z-index: -1;
        width: .5em;
      }
    }
  }

  .why{
    margin-top: 0;
    &__inner{
      padding: 3em 1.75em 0;
      clip-path: polygon( 0% 1.998%,0% 1.998%,0.081% 1.659%,0.316% 1.34%,0.689% 1.047%,1.186% 0.783%,1.793% 0.554%,2.494% 0.364%,3.274% 0.219%,4.121% 0.122%,5.017% 0.079%,5.95% 0.094%,95.423% 4.391%,95.423% 4.391%,96.188% 4.449%,96.905% 4.545%,97.567% 4.676%,98.166% 4.838%,98.694% 5.029%,99.143% 5.245%,99.506% 5.483%,99.775% 5.74%,99.943% 6.012%,100% 6.296%,100% 98.002%,100% 98.002%,99.919% 98.341%,99.684% 98.66%,99.311% 98.954%,98.814% 99.217%,98.207% 99.446%,97.506% 99.636%,96.726% 99.781%,95.88% 99.878%,94.983% 99.921%,94.05% 99.907%,4.577% 95.609%,4.577% 95.609%,3.812% 95.551%,3.095% 95.455%,2.433% 95.324%,1.834% 95.162%,1.306% 94.971%,0.857% 94.755%,0.494% 94.517%,0.225% 94.26%,0.057% 93.988%,0% 93.704%,0% 1.998% );
    }
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(2, calc(50% - 1.25em));
      gap: 2.5em;
    }
    &__img{
      position: static;
      margin: -1.875em 0 -1.75em auto;
      width: calc(100% + 2.5em);
      max-width: 25em;
      display: block;
    }
  }
}


@media screen and (max-width: 576px){
  .why{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
