.how-work2{
  .how-work__top{
    text-align: left;
    margin: 0;
  }
  @media screen and (min-width: 993px){
    .how-work__steps{
      grid-template-columns: repeat(6,calc(100% / 6));
      gap: 0;
    }
  }
}

.consult{
  @media screen and (min-width: 993px){
    .container{
      padding: 0 5.875em;
    }
  }
}

.consult-form{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2.375em / 3));
  gap: 1.1875em;
  margin-top: 1.25em;
  &__btn{
    height: 3.6875em;
  }
}


@media screen and (max-width: 992px){
  .how-work2{
    .how-work__steps{
      gap: 1.81em;
    }
  }

  .consult-form{
    grid-template-columns: repeat(2,calc(50% - 1.1875em / 2));
    &__btn{
      grid-column: 1/-1;
    }
  }
}


@media screen and (max-width: 576px){
  .consult-form{
    grid-template-columns: 100%;
  }
}